.row {
    color: white;
    margin-left: 20px;

}

.row_poster {
    width: 100%;
    object-fit: contain;
    max-height: 120px;
    transition: transform 500ms;
    padding: 10px;
}

.row_title {
    padding-left: 20px;
}

.row_poster_Large {
    width: 100%;
    object-fit: contain;
    max-height: 250px;
    transition: transform 500ms;
    padding: 15px;
}

.row_posters {
    display: flex;    
    overflow-y: hidden;
    overflow-x: scroll;
}



.row_title_Large {
    padding-top: 30px
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_poster:hover {
    transform: scale(1.08);;
}

.row_poster_Large:hover {
    transform: scale(1.1);;
}

.row_poster_info {
    padding-top: 15px;
    padding-left: 20px;
    height: fit-content;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 90%;
}

.info_title {
    font-size: 2.5rem;
    margin-bottom: 15px;
}

.info_overview {
    padding-top: 5px;
}

.info_desc {
    max-width: 50rem;
}

.info_rating {
    display: flex;
    align-items: center;
}

.imdb_logo {
    object-fit: contain;
    width: 30px;
    margin-left: 5px;
}

.info_image {
    object-fit: contain;
    width: 200px;
}